.Education {
  border-top: 5px solid #daf6ff;
}

.Education-left-half {
  background-color: #86a7b3;
  position: absolute;
  left: 0px;
  width: 30%;
  text-align: center;
  font-family: "montserrat", sans-serif;
}

.Education-right-half {
  background-color: #f2fcff;
  position: absolute;
  right: 0px;
  width: 70%;
  font-family: "montserrat", sans-serif;
}

.Education-right-half h1 {
  text-align: left;
  padding-top: 70px;
  padding-bottom: 20px;
}

.Education-left-half p {
  padding: 20px;
  padding-top: 60px;
  text-align: left;
}

/* .fa-graduation-cap {
  padding: 50px;
} */

.Phone-Details {
  text-align: left;
  margin-top: 50px;
  padding: 8px;
  border-radius: 10px;
}

.Phone-Details:hover {
  background-color: #f2fcff;
}

.Phone-Details a,
.Phone-Details a:active {
  text-decoration: none;
  color: #000000;
}

.Contact-Links:visited {
  color: #050505;
  text-decoration: none;
}

.Contact-Links,
.Contact-Links:active {
  color: #000000;
  text-decoration: none;
}

.divide {
  border-top: 1px solid #000000;
}

.Education-Section {
  text-align: left;
  padding: 28px;
}
